body {
  background-color: #000;
  font-family:'Courier New', Courier, monospace;
  color: #fff;
}

#root {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.board {
  display: flex;
  flex-direction: row;
}

.row {
  box-sizing: border-box;
}

.row-label {
  display: inline-block;
  text-align: right;
  vertical-align: top;
}

.dot {
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}

.feedback-area {
  display: inline-block;
  vertical-align: top;
}

.check-button {
  border: 6px solid #bbb;
  background-color: none;
  border-radius: 20%;
  vertical-align: top;
  cursor: pointer;
}

.replay {
  border: 6px solid #fff;
  color: #fff;
  background-color: #000;
  border-radius: 1em;
  width: 100%;
  font-size: 1em;
  cursor: pointer;
}

.feedback-view {
  display: inline-block;
  box-sizing: border-box;
}

.feedback-peg {
  display: inline-block;
}

/* .row.active {
  background-color: #99cfeb;
} */

.palette {
  background-color: #000;
  margin: 0.25em;
}

.palette-item {
  display: block;
}

.info {
  color: #fff;
}

.warning {
  color: #fec601;
}
